

@font-face {
  font-family: Star Jedi;
  src: url('./star-jedi_[allfont.net].ttf') format("truetype");
  }

  @font-face {
    font-family: Planet;
    src: url('./planet-n_[allfont.net].ttf') format("truetype");
    }


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.titulo{
  font-family: Star Jedi, arial;
}

.text{
  font-family: Planet, arial;
}

